import { statelessComponent, render, update, connect, map } from "ivi";
import * as h from "ivi-html";
import { DBList } from "./db";
import { startFPSMonitor, startMemMonitor, initProfiler, startProfile, endProfile } from "perf-monitor";
var mutations = 0.5;
var N = 50;
var qs = parseQueryString(window.location.search.substr(1).split("&"));
if (qs["n"] !== undefined) {
    N = parseInt(qs["n"], 10);
}
if (qs["m"] !== undefined) {
    mutations = parseFloat(qs["m"]);
}
var store = new DBList(N);
function entryFormatElapsed(v) {
    if (v === 0) {
        return "";
    }
    if (v > 60) {
        var minutes = Math.floor(v / 60);
        var comps = (v % 60).toFixed(2).split(".");
        var seconds = comps[0];
        var ms = comps[1];
        return minutes + ":" + seconds + "." + ms;
    }
    return v.toFixed(2);
}
function counterClasses(count) {
    if (count >= 20) {
        return "label label-important";
    }
    else if (count >= 10) {
        return "label label-warning";
    }
    return "label label-success";
}
function queryClasses(elapsed) {
    if (elapsed > 0) {
        if (elapsed >= 10.0) {
            return "Query elapsed warn_long";
        }
        else if (elapsed >= 1.0) {
            return "Query elapsed warn";
        }
        return "Query elapsed short";
    }
    return "";
}
var Popover = statelessComponent(function (query) { return (h.div("popover left").c(h.div("popover-content").c(query), h.div("arrow"))); });
var DatabaseList = connect(function (prev, props) {
    var db = store.dbs[props];
    if (prev && prev.db === db) {
        return prev;
    }
    return { db: db };
}, function (props) {
    var db = props.db;
    var topFiveQueries = db.getTopFiveQueries();
    var count = db.queries.length;
    return h.tr().c(h.td("dbname").c(db.name), h.td("query-count").c(h.span(counterClasses(count)).c(count)), map(topFiveQueries, function (q, i) { return (h.td(queryClasses(q.elapsed)).k(i).c(entryFormatElapsed(q.elapsed), Popover(q.query))); }));
});
var Main = statelessComponent(function (props) { return (h.table("table table-striped latest-data").c(h.tbody().c(map(props.dbs, function (db, i) { return DatabaseList(i).k(db.id); })))); });
function parseQueryString(a) {
    if (a.length === 0) {
        return {};
    }
    var b = {};
    for (var i = 0; i < a.length; ++i) {
        var p = a[i].split("=", 2);
        if (p.length === 1) {
            b[p[0]] = "";
        }
        else {
            b[p[0]] = decodeURIComponent(p[1].replace(/\+/g, " "));
        }
    }
    return b;
}
document.addEventListener("DOMContentLoaded", function () {
    startFPSMonitor();
    startMemMonitor();
    initProfiler("view update");
    var sliderContainer = document.createElement("div");
    sliderContainer.style.display = "flex";
    var slider = document.createElement("input");
    slider.type = "range";
    slider.style.marginBottom = "10px";
    slider.style.marginTop = "5px";
    var text = document.createElement("label");
    text.textContent = "mutations : " + (mutations * 100).toFixed(0) + "%";
    slider.addEventListener("change", function (e) {
        mutations = Number.parseFloat(e.target.value) / 100;
        text.textContent = "mutations : " + (mutations * 100).toFixed(0) + "%";
    });
    sliderContainer.appendChild(text);
    sliderContainer.appendChild(slider);
    document.body.insertBefore(sliderContainer, document.body.firstChild);
    var container = document.getElementById("app");
    render(Main(store), container);
    function tick() {
        store.randomUpdate(mutations);
        startProfile("view update");
        update();
        endProfile("view update");
        setTimeout(tick, 0);
    }
    setTimeout(tick, 0);
});
